/* import fonts*/

@import url('https://fonts.googleapis.com/css?family=PT+Sans');

@font-face {
  font-family: "Proxima Nova";
  src: url(data:font/opentype;base64,);
  font-style: normal;
  font-weight: 700;
}

@font-face {
  font-family: "Proxima Nova";
  src: url(data:font/opentype;base64,);
  font-style: normal;
  font-weight: 600;
}

/* color scheme */

$main-color: #9f9acf;
$additional-color: #ebebeb;
$content-color: #665f60;
$light-content-content: #fefcfe;

/* fonts scheme*/

$section-title-font: 700 36px "Proxima Nova", sans-serif;
$section-subtitle-font: 24px 'PT Sans', sans-serif;
$content-font: 400 18px 'PT Sans', sans-serif;


.flex-container{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

@mixin title-section ($color) {
  font: $section-title-font;
  text-transform: uppercase;
  color: $color;
}

@mixin simple-button ($color){
  border: 1px solid $color;
  color: $color;
  font-style: italic;
  font-size: 20px;
  width: 193px;
  height: 48px;
  &:hover{
   box-shadow: 0 0 20px $content-color;
  }
}
