/* icons - animation*/
.social-bar {
  width: 32px;
  height: 32px;
  display: inline-block;
  content: " ";
  border-radius: 50%;
  position: relative;
  overflow: hidden;
}

.social-bar:after {
  position: absolute;
  transition: .3s;
  content: '';
  width: 0;
  bottom: 0;
  opacity: .6;
  height: 120%;
  left: -10%;
  transform: skewX(25deg);
}

.social-bar:hover:after {
  width: 120%;
}

.social-bar--behance {
  background: url("../images/social/behance.svg");
}

.social-bar--behance:after {
  background: #00b2ff;
}

.social-bar--linked {
  background: url("../images/social/linked-in.svg");
}

.social-bar--linked:after {
  background: #0077b5;
}

.social-bar--google {
  background: url("../images/social/google.svg");
}

.social-bar--google:after {
  background: #ea4335;
}

.social-bar--facebook {
  background: url("../images/social/facebook.svg");
}

.social-bar--facebook:after {
  background: #3b5998;
}

.social-bar--twitter {
  background: url("../images/social/twitter.svg");
}

.social-bar--twitter:after {
  background: #00b2ff;
}

.social-bar + .social-bar {
  margin-left: 25px;
}
